@import '../variables';

.divNavbar {
    display: flex;
    background-color: $dark-color;
    height: 4em;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 0 1em;
    z-index: 20; }

.logoNavbar {
    display: flex;
    height: 2.5em; }

.divIconsNavbar {
    display: flex;
    height: 2.5em;
    width: 40%;
    justify-content: space-between; }

.iconsNavbar {
    height: 2.3em; }

.svgNavbar {
    &:hover {
        fill: $yellow-color; } }

.svgIcons {
    fill: #fff; }

.picProfile {
    border-radius: 100%; }

.dropdown {
  position: relative;
  display: inline-block;
  float: right; }


.dropdown-content {
  display: none;
  position: absolute;
  right: 0;
  background-color: $light-grey-color;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 15;
  font-family: $font-regular; }

.dropdown-content {
    p {
        color: $dark-color;
        padding: 12px 16px;
        display: block;
        margin: 0;
        cursor: pointer; } }

.dropdown-content {
    p {
        &:hover {
            background-color: $yellow-color; } } }

.dropdown {
    &:hover {
        .dropdown-content {
            display: block; } } }

.bottomNavbar {
    background-color: $dark-color;
    color: #fff;
    height: 4em;
    display: none;
    position: fixed;
    bottom: 0;
    z-index: 20;
    width: 100%; }

.itemMenu .tooltiptext {
    visibility: hidden;
    background-color: #fff;
    color: $dark-color;
    text-align: center;
    padding: 0.5em;
    border: $border-grey;
    border-radius: $border-radius;
    font-family: $font-regular;
    font-size: 10px;
    margin-left: -5em;
    margin-top: 4.2em;

    position: absolute;
    z-index: 22; }

.itemMenu {
    &:hover {
        .tooltiptext {
            visibility: visible; } } }



@media screen and (max-width: 550px) {
    .bottomNavbar {
        display: flex;
        justify-content: space-around;
        align-items: center; }

    .divIconsNavbar > .svgNavbar {
        display: none; }

    .divIconsNavbar {
        justify-content: flex-end; } }


