@import '../variables';

@mixin inputModal {
    border: none;
    border-radius: $border-radius;
    padding: 1em;
    resize: none;
    background-color: $light-grey-color;
    font-size: 13px;
    font-family: $font-regular; }

.modal {
    position: fixed;
    z-index: 10;
    padding-top: 100px;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(0,0,0,0.4); }

.modal-content {
    display: flex;
    flex-direction: column;
    background-color: #fff;
    padding: 0.7em 2em 2em 2em;
    margin: auto;
    border-radius: $border-radius;
    width: 40%;
    font-family: $font-regular;
    max-width: 500px; }

.headerModal {
    display: flex;
    justify-content: space-between; }

.textareaModal {
    @include inputModal();
    height: 6em; }

.sendButton {
    width: 2em;
    width: 1.7em;
    margin-top: 1em;
    align-self: flex-end;
    cursor: pointer; }

.close {
    color: $dark-color;
    float: right;
    font-size: 20px;
    font-weight: bold;
    display: flex;
    align-self: flex-end;
    &:hover {
        color: $medium-grey;
        text-decoration: none;
        cursor: pointer; } }

.file-div {
    display: flex;
    margin-top: 0.7em;
    align-items: center;
    font-size: 12px; }

.tag-post {
    @include inputModal();
    font-size: 10px;
    margin: 0 1em; }

@media screen and (max-width: 550px) {
    .modal-content {
        width: 80%; } }
