@import url(https://fonts.googleapis.com/css2?family=Open+Sans+Condensed:wght@300&family=Open+Sans:wght@300;400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{color:#111106;margin:auto;height:100vh}

.button{color:#111106;width:15em;padding:0.5em;margin-top:1em;font-size:14px;border:none;font-family:"Open Sans",sans-serif;font-weight:400;border-radius:8px;background-color:#fac937}

.backgroungAuth{width:100%;height:100vh;background-image:url(/SCL012-Social-Network-Frameworks/static/media/back-1.d5bba14d.jpg);background-attachment:fixed;background-position:center;display:flex;justify-content:center;align-items:center;font-family:"Open Sans",sans-serif;color:#111106}.containerAuth{background-color:#f0f0f0;border-radius:8px;display:flex;flex-direction:column;padding:1em 2em;width:300px;align-items:center}.logoAuth{width:70%;height:auto}.link{display:flex;justify-content:center}.titleAuth{color:#111106;font-size:30px;margin-bottom:0;align-self:start}.inputsAuth{border:#b6b6b6 1px solid;background-color:#f0f0f0;border-radius:8px;padding:1em;margin-top:1em;width:95%}.buttonAuth{color:#111106;width:15em;padding:0.5em;margin-top:1em;font-size:14px;border:none;font-weight:400;border-radius:8px}.buttonGoogle{border:#b6b6b6 1px solid;background-color:#f0f0f0;display:flex;align-content:center;justify-content:center}.forgotPass{font-size:11px;margin-top:0.5em;text-decoration:none;color:#717171}.forgotPass:hover{color:#111106}.googleLogo{width:1em;margin-left:6px;align-self:center}.toggleAuth{font-size:14px}@media (max-width: 650px){.containerAuth{width:60%}}

.titleWelcome{font-family:"Open Sans",sans-serif;color:#111106}.textWelcome{font-family:"Open Sans Condensed",sans-serif;font-size:24px;color:#111106;margin-top:0}

.divNavbar{display:flex;background-color:#111106;height:4em;flex-direction:row;justify-content:space-between;align-items:center;padding:0 1em;z-index:20}.logoNavbar{display:flex;height:2.5em}.divIconsNavbar{display:flex;height:2.5em;width:40%;justify-content:space-between}.iconsNavbar{height:2.3em}.svgNavbar:hover{fill:#fac937}.svgIcons{fill:#fff}.picProfile{border-radius:100%}.dropdown{position:relative;display:inline-block;float:right}.dropdown-content{display:none;position:absolute;right:0;background-color:#f0f0f0;min-width:160px;box-shadow:0px 8px 16px 0px rgba(0,0,0,0.2);z-index:15;font-family:"Open Sans",sans-serif}.dropdown-content p{color:#111106;padding:12px 16px;display:block;margin:0;cursor:pointer}.dropdown-content p:hover{background-color:#fac937}.dropdown:hover .dropdown-content{display:block}.bottomNavbar{background-color:#111106;color:#fff;height:4em;display:none;position:fixed;bottom:0;z-index:20;width:100%}.itemMenu .tooltiptext{visibility:hidden;background-color:#fff;color:#111106;text-align:center;padding:0.5em;border:#b6b6b6 1px solid;border-radius:8px;font-family:"Open Sans",sans-serif;font-size:10px;margin-left:-5em;margin-top:4.2em;position:absolute;z-index:22}.itemMenu:hover .tooltiptext{visibility:visible}@media screen and (max-width: 550px){.bottomNavbar{display:flex;justify-content:space-around;align-items:center}.divIconsNavbar>.svgNavbar{display:none}.divIconsNavbar{justify-content:flex-end}}

.divPost{width:260px;background-color:#f0f0f0;display:flex;flex-direction:column;color:#111106}.imgPost{width:260px}.contentPost{padding:0.8em 0.8em 1.2em 0.8em}.flexRow{display:flex;flex-direction:row;align-items:center;justify-content:space-between}.idPost{margin-bottom:0.5em;font-family:"Open Sans",sans-serif;font-size:18px}.textPost{font-family:"Open Sans Condensed",sans-serif}.tagsPost{font-family:"Open Sans",sans-serif;color:#b6b6b6;font-size:13px;margin:1em 0 0 0;cursor:pointer}.tagsPost:hover{color:#111106}.tagsPost::before{content:'#'}.userPicPost{height:1.5em;border-radius:100%;margin-right:0.4em}.heartLike{height:1em;margin-left:0.4em;fill:#fff}.likedPost{fill:#fac937}.margin0{margin:0;font-weight:400}

.containerPosts{margin-top:1.5em;margin-bottom:5em}.titleHome{font-family:"Open Sans Condensed",sans-serif;margin:1em 0.8em;background-color:#fac937;color:#111106;border-radius:8px;padding:0.2em 0.5em;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}

.modal{position:fixed;z-index:10;padding-top:100px;left:0;top:0;width:100%;height:100%;overflow:auto;background-color:rgba(0,0,0,0.4)}.modal-content{display:flex;flex-direction:column;background-color:#fff;padding:0.7em 2em 2em 2em;margin:auto;border-radius:8px;width:40%;font-family:"Open Sans",sans-serif;max-width:500px}.headerModal{display:flex;justify-content:space-between}.textareaModal{border:none;border-radius:8px;padding:1em;resize:none;background-color:#f0f0f0;font-size:13px;font-family:"Open Sans",sans-serif;height:6em}.sendButton{width:2em;width:1.7em;margin-top:1em;align-self:flex-end;cursor:pointer}.close{color:#111106;float:right;font-size:20px;font-weight:bold;display:flex;align-self:flex-end}.close:hover{color:#b6b6b6;text-decoration:none;cursor:pointer}.file-div{display:flex;margin-top:0.7em;align-items:center;font-size:12px}.tag-post{border:none;border-radius:8px;padding:1em;resize:none;background-color:#f0f0f0;font-size:13px;font-family:"Open Sans",sans-serif;font-size:10px;margin:0 1em}@media screen and (max-width: 550px){.modal-content{width:80%}}

.overlayZoom{z-index:14;width:100%;height:100%;position:fixed;display:flex;justify-content:center;padding-top:0.5em}.divZoom{z-index:15;display:flex;flex-direction:column;color:#111106;height:100%;max-width:90%;position:fixed;max-height:90%}.imgZoom{max-height:70%;max-width:100%}.contentPost.contentZoom{max-height:40%;background-color:#f0f0f0;height:auto}.closeZoom{background-color:#f0f0f0;position:absolute;padding:0.5em 0.8em 0.6em 0.85em;border-radius:100%;right:0;margin:0.5em;cursor:pointer;font-weight:bold}.textPost.margin0{max-width:-webkit-fit-content;max-width:-moz-fit-content;max-width:fit-content}@media screen and (max-width: 500px){.divZoom{height:88%}}

